<template>
	<view class="container" v-if="isshow">
		<view class="myInfo">
			<view class="infoList infoHead">
				<label>头像</label>
				<view class="flexBet">
					<view class="infoImg"><image @click="modifyHead" :src="form.headSculpture ? form.headSculpture : headerImg" mode="aspectFill"></image></view>
					<!-- <view class="infoImg"><image @click="modifyHead" src="http://192.168.0.224/appUser/loadHeadImg?keyName=appUser\7.jpg" mode="aspectFill"></image></view> -->
					<uni-icons type="arrowright" size="12" color="#999"></uni-icons>
				</view>
			</view>
			<view class="infoList">
				<label>姓名</label>
				<view class="flexBet">
					<input v-model="form.username" type="text" placeholder="请填写姓名" />
					<uni-icons type="arrowright" size="12" color="#999"></uni-icons>
				</view>
			</view>
			<view class="infoList">
				<label>性别</label>
				<picker @change="sexChange" :value="form.sex" :range="sexList">
					<view class="flexBet">
						<input type="text" disabled="true" placeholder="请选择性别" :value="sexList[form.sex]" placeholder-class="phcolor" />
						<uni-icons type="arrowright" size="12" color="#999"></uni-icons>
					</view>
				</picker>
			</view>
			<view class="infoList">
				<label>年龄</label>
				<view class="flexBet">
					<input v-model="form.age" type="number" placeholder="请填写年龄" />
					<uni-icons type="arrowright" size="12" color="#999"></uni-icons>
				</view>
			</view>
			<view class="infoList">
				<label>手机号</label>
				<view class="flexBet">
					<input v-model="form.phoneNum" type="number" maxlength="11" placeholder="请填写手机号" />
					<uni-icons type="arrowright" size="12" color="#999"></uni-icons>
				</view>
			</view>
			<view class="infoList">
				<label>邮箱</label>
				<view class="flexBet">
					<input v-model="form.email" type="number" placeholder="请填写邮箱" />
					<uni-icons type="arrowright" size="12" color="#999"></uni-icons>
				</view>
			</view>
			
			<view class="infoList infoBorder">
				<label>公司</label>
				<text class="noModify">{{form.companyName?form.companyName:'暂无'}}</text>
			</view>
			<view class="infoList">
				<label>职位</label>
				<text class="noModify">{{form.positionName?form.positionName:'暂无'}}</text>
			</view>
		</view>
		<view class="footer" @click="sureModify">确定</view>
	</view>
</template>

<script>
	import header from '@/static/moreIcon/avatar.png';
	import ldSelect from '@/components/ld-select/ld-select.vue';
	import config from "@/utils/config.js";
	import httpUtils from '@/utils/httpUtils.js';
	import Tools from '@/utils/tools.js';
	export default {
		data() {
			return {
				isshow:false,
				form: {
					headSculpture: '', // 头像
					username: '', // 姓名
					phoneNum: '', // 手机
					email: '', // 邮箱
					sex: '', // 性别
					age: '', // 年龄
					companyName: '', // 公司
					positionName: '', // 职位
				},
				saveHeadSculpture:'', // 保存时提交的头像
				sexList: ['保密', '男', '女'], // 性别list
				headerImg: header 
			}
		},
		onLoad(){
			this._getInfo()
		},
		methods: {
			// 获取个人信息
			_getInfo(){
				let that = this
				Tools.showLoading('加载中')
				httpUtils.sendHttpRequest('1005', Object.assign({}, ''), function(res) {
					Tools.hideLoading()
					if (res.data.code == '0') {
						that.isshow = true
						that.form = res.data.data
					}
				})
			},
			// 上传头像
			modifyHead() {
				uni.chooseImage({
					sizeType: ['original', 'compressed'], //可以指定是原图还是压缩图，默认二者都有
					sourceType: ['album', 'camera'], //从相册选择
					count: 1,
					success: (res) => {
						let imgurl = res.tempFiles[0].path
						Tools.showLoading('图片上传中')
						uni.uploadFile({
							url: config.serveData.apiServerUrl + '/appUser/headImgUpload',
							filePath: imgurl,
							name: 'file',
							header: {
								"Content-Type": "application/json",
								"token": uni.getStorageSync('loginInfo').token || ''
							},
							formData: null,
							success: (res) => {
								Tools.hideLoading()
								let uploadData = JSON.parse(res.data)
								if(uploadData.code == 0){
									this.form.headSculpture = uploadData.data.url2
									this.saveHeadSculpture = uploadData.data.url1
								}else{
									Tools.showErrorToast(uploadData.msg)
								}
							}
						})
					}
				})
			},
			// 性别选择
			sexChange: function(e) {
				this.form.sex = e.target.value
			},
			// 修改个人信息
			sureModify(){
				let that = this
				Tools.showLoading('修改中')
				that.form.headSculpture = that.saveHeadSculpture
				httpUtils.sendHttpRequest('1006', Object.assign({}, that.form), function(res) {
					Tools.hideLoading()
					if (res.data.code == '0') {
						let loginInfo = uni.getStorageSync('loginInfo')
						loginInfo.username = that.form.username;
						loginInfo.headSculpture = that.form.headSculpture;
						uni.setStorageSync('loginInfo', loginInfo);
						setTimeout(function(){
							Tools.showSuccessToast(res.msg)
							uni.navigateBack({
								delta:1
							})
							let pages = getCurrentPages();
							let beforePage = pages[pages.length - 3];
							beforePage.onLoad()
						},500)
					}
				})
			}
		},
	}
</script>

<style lang="scss">
	@import '@/styles/form.scss';

	page {
		background-color: #fff;
	}
	.container {
		.myInfo {
			.infoHead {
				image {
					width: 120rpx;
					height: 120rpx;
					border-radius: 50%;
				}
			}
			.infoList {
				display: flex;
				align-items: center;
				justify-content: space-between;
				font-size: 28rpx;
				padding: 30rpx;
				border-bottom: 1rpx solid #eee;

				label {
					color: #333;
					width: 200rpx;
					display: inline-block;
				}

				input{
					width: 100%;
					text-align: right;
					margin-right: 20rpx;
					color: #666;
				}
				.noModify{
					width: 100%;
					text-align: right;
					color: #666;
				}
				.infoImg{
					width: 100%;
					text-align: right;
					margin-right: 20rpx;
				}
			}
			.infoBorder{
				border-top: 20rpx solid #f6f6f6;
			}
		}
	}
</style>
